  addExperience() {
    let obj = {
      type: "addExperience",
      userId: this.appmin.getUserid()
    }
  //  this.appmin.send(JSON.stringify(obj));

    this.appmin.get(obj).subscribe((data) => {
      this.dataHandle(data);
    })
    
  }

  ngOnInit() {
    this.addExperience();
  }

